import React, {useState} from 'react';
import './FilterComponent.css'
import Divider from "../template/Divider";
import NavigationLeft from "./NavigationLeft";

export const NavLeftMenu = ({focusTag, type}) => {
    return <div className='filter-component-menu'>
        <div className='filter-component-menu-title'>DANH MỤC SẢN PHẨM</div>
        <div className='filter-component-menu-body'>
            <NavigationLeft focusTag={focusTag} type={type}/>
        </div>
    </div>
}
const FilterComponent = ({onFilterChange, focusTag, type}) => {
    const [state, setState] = useState({
        is_new: 0,
        is_hot: 0,
        price_from: 0,
        price_to: 999999999,
    })
    const handleInputChange = (e) => {
        let name = e.target.name
        let value = null
        if (name === 'is_hot' | name === 'is_new') {
            value = e.target.checked ? 1 : 0
        } else {
            value = e.target.value
        }
        setState({
            ...state,
            [name]: value
        })
    }
    const handleSubmitClick = () => {
        if (state.price_from > state.price_to || state.price_to === 0) {
            alert('Invalid price range')
            return
        }
        onFilterChange(state)
    }
    const handleReset = () => {
        setState({
            is_new: 0,
            is_hot: 0,
            price_from: 0,
            price_to: 999999999,
        })
        onFilterChange({
            is_new: 0,
            is_hot: 0,
            price_from: 0,
            price_to: 999999999,
        })
    }
    const handleMinChange = (min) => {
        setState({
            ...state,
            price_from: min
        })
    }
    const handleMaxChange = (max) => {
        setState({
            ...state,
            price_to: max
        })
    }
    return (
        <div className='filter-component-container'>
            <NavLeftMenu focusTag={focusTag} type={type} />
            <div className='filter-component-title-container'>
                <i className="fas fa-filter"></i>
                <span className='filter-component-title'>
                BỘ LỌC TÌM KIẾM
                </span>
            </div>
            <div className='filter-component-type-container'>
                <div>Loại sản phẩm</div>
                <div className='filter-component-checkbox'>
                    <input type='checkbox' className='filter-component-checkbox-1' name='is_new' checked={state.is_new}
                           onChange={handleInputChange}/> <span>Sản phẩm mới</span>
                </div>
                <div className='filter-component-checkbox'>
                    <input type='checkbox' className='filter-component-checkbox-1' name='is_hot' checked={state.is_hot}
                           onChange={handleInputChange}/> <span>Sản phẩm bán chạy</span>
                </div>
            </div>
            <div className='filter-component-price-container'>
                <div>Khoảng giá</div>
                {/*<div style={{marginBottom:'1em', boxSizing:"border-box"}}>*/}
                {/*    <SliderComponent total={999999999} currentMin={state.price_from}*/}
                {/*                     currentMax={state.price_to}*/}
                {/*                     onMinChange={handleMinChange} onMaxChange={handleMaxChange}/>*/}
                {/*</div>*/}
                <div className='filter-component-input'>
                    <div>
                        <input type='number' name='price_from' onChange={handleInputChange} value={state.price_from}/>
                    </div>
                    <span className='filter-component-input-divider'>~</span>
                    <div>
                        <input type='number' name='price_to' onChange={handleInputChange} value={state.price_to}/>
                    </div>
                </div>
            </div>
            <Divider/>
            <button className='filter-component-button' onClick={handleSubmitClick}>Áp dụng
            </button>
            <button className='filter-component-button' onClick={handleReset}>Đặt lại
            </button>
        </div>
    );
};

export default FilterComponent;