import React from "react";
import SkeletonTemplate from "../template/Skeleton/SkeletonTemplate";
import './CategorySkeleton.css'
import BodyTemplate from "../template/BodyTemplate";

const CategorySkeleton = ({routeTemplate, leftComponent, rightComponent}) => {
    return (
        <React.Fragment>
            <div className="category-slide-skeleton-container">
                <div className='category-slide-skeleton-element'>
                    <SkeletonTemplate animation={true} width="100%" height="100%"/>
                </div>
            </div>
            <SkeletonTemplate
                animation={true}
                width="100%"
                height="3em"
                extraStyle={{marginTop: "1em", marginBottom:"1em"}}
            />
            <div style={{display: "flex"}}>
                <div className="category-left">
                    <div
                        style={{
                            width: "95%",
                            height: "300px",
                            marginTop: "1em",
                        }}
                    >
                        <SkeletonTemplate width="100%" height="100%" animation={true}/>
                    </div>
                </div>
                <div className="category-right">
                    <div
                        style={{
                            width: "100%",
                            height: "1000px",
                            marginTop: "1em",
                        }}
                    >
                        <SkeletonTemplate width="100%" height="100%" animation={true}/>
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
};

export default CategorySkeleton;