import React from 'react';
import './Category.css'
import Slide from "../home/components/Slide";
import {useSelector} from "react-redux";
export const getBanner = (navigation) =>{
    let list = []
    for (var i = 0 ; i< navigation.length; i++)
    {
        if (navigation[i].banner)
        {
            list.push({
                url: navigation[i].banner,
                path: navigation[i].path,
            })
        }
    }
    return list;
}
const Category = ({routeTemplate, leftComponent, rightComponent}) => {
    const navigation = useSelector(state=>state.prefetchDataReducer.navigation)
    const listBanner = getBanner(navigation)
    return (
        <div className={'category-container-root'}>
            <Slide data={listBanner} aspect_ratio = {3.5} timeout={10000}></Slide>
            {routeTemplate}
            <div style={{display: 'flex'}}>
                <div className='category-left'>
                    {leftComponent}
                </div>
                <div className='category-right'>
                    {rightComponent}
                </div>
            </div>

        </div>
    );
};

export default Category;