import React, {useState, useRef, useEffect} from "react";
import "./Slide.css";
import {useHistory} from 'react-router-dom'

export default function Slide({data, aspect_ratio, timeout}) {
    const [slide, setSlide] = useState(0);
    const intervalSlide = useRef(null);
    const history = useHistory()
    useEffect(() => {
        intervalSlide.current = setTimeout(() => {
            slide === data.length - 1
                ? setSlide(0)
                : setSlide((prevState) => prevState + 1);
        }, timeout);
        return () => clearTimeout(intervalSlide.current);
    }, [slide]);

    function handleLeftArrow() {
        if (intervalSlide.current) clearTimeout(intervalSlide.current);

        slide === 0
            ? setSlide(data.length - 1)
            : setSlide((prevState) => prevState - 1);
    }

    function handleRightArrow() {
        if (intervalSlide.current) clearTimeout(intervalSlide.current);
        slide === data.length - 1
            ? setSlide(0)
            : setSlide((prevState) => prevState + 1);
    }

    return (
        <div className="slide-container">
            <div
                className="slide-slides-images"
                style={{
                    marginLeft: `${(-slide) * 100}%`
                }}
            >
                {data.map((item, index) => (
                        <div
                            key={index}
                            className="slide-image"
                        >
                            <div className='slide-image-element' style={{
                                backgroundImage: `url(${item.url})`,
                                paddingTop: `${100 / aspect_ratio}%`
                                // height: '500px'
                            }} onClick={()=>{history.push(`/p/${item.path}`)}}>
                                <div className='slide-text-inner'>1</div>
                            </div>
                        </div>
                ))}
            </div>
            <div
                className="slide-slides-arrow-container left-arrow"
                onClick={handleLeftArrow}
            >
                <i className="fas fa-chevron-left"></i>
            </div>
            <div
                className="slide-slides-arrow-container right-arrow"
                onClick={handleRightArrow}
            >
                <i className="fas fa-chevron-right"></i>
            </div>
        </div>
    );
}
